import { useRouter } from "next/router";
import { AuthContext } from "../../context/AuthContext";
import { useEffect, useRef, useState } from "react";
import Peer from "peerjs";

const MeetingPage = () => {
  const router = useRouter();
  const { id: meetingId } = router.query;
  const [peer, setPeer] = useState<Peer | null>(null);
  const [socket, setSocket] = useState<WebSocket | null>(null);
  const [localStream, setLocalStream] = useState<MediaStream | null>(null); // âœ… Store stream in state
  const [meetingLink, setMeetingLink] = useState<string | null>(null);
  const [copied, setCopied] = useState(false);
  const [isMuted, setIsMuted] = useState(false);
  const [isVideoOff, setIsVideoOff] = useState(false);
  const myVideoRef = useRef<HTMLVideoElement>(null);
  const remoteVideosRef = useRef<{ [key: string]: HTMLVideoElement }>({});
  const [isAdmin, setIsAdmin] = useState(false);
  const [pendingUsers, setPendingUsers] = useState<string[]>([]);
  useEffect(() => {
    if (!socket || !peer) return;
  
    socket.onmessage = (event) => {
      const data = JSON.parse(event.data);
  
      if (data.type === "approval-request" && isAdmin) {
        setPendingUsers((prev) => [...prev, data.peerId]);
      }
    };
  }, [socket, peer]);
  
  const approveUser = (peerId: string) => {
    setPendingUsers((prev) => prev.filter((id) => id !== peerId));
  
    if (socket) { // âœ… Ensure socket is not null before calling send()
      socket.send(JSON.stringify({ type: "approved", peerId }));
    } else {
      console.warn("âš ï¸ WebSocket is not connected, cannot send approval.");
    }
  };
  
  useEffect(() => {
    if (!meetingId) return;

    const websocketURL = process.env.NEXT_PUBLIC_WEBSOCKET_URL || "ws://localhost:8000";
    const frontendURL = process.env.NEXT_PUBLIC_FRONTEND_URL || "http://localhost:3000";

    setMeetingLink(`${frontendURL}/meeting/${meetingId}`);

    const userPeerId = Math.random().toString(36).substring(7);
    const newSocket = new WebSocket(`${websocketURL}/ws/${meetingId}/${userPeerId}`);

    newSocket.onopen = () => {
        console.log("âœ… WebSocket Connected!");
        newSocket.send(JSON.stringify({ type: "new-user", peerId: userPeerId }));
    };

    newSocket.onmessage = (event) => {
        const data = JSON.parse(event.data);
        console.log("ðŸ“© WebSocket Message:", data);

        if (data.type === "new-user" && data.peerId !== userPeerId) {
            console.log(`ðŸ”” New participant joined: ${data.peerId}`);
            callUser(data.peerId); // âœ… Ensure we call the new user
        }
    };

    setSocket(newSocket);

    return () => {
        newSocket.close();
    };
}, [meetingId]); 


// âœ… Move getUserMedia() to a separate effect so it only runs once
useEffect(() => {
    if (localStream || myVideoRef.current?.srcObject) return; // âœ… Prevent multiple calls

    navigator.mediaDevices.getUserMedia({ video: true, audio: true })
        .then((stream) => {
            setLocalStream(stream);
            if (myVideoRef.current) {
                myVideoRef.current.srcObject = stream;
            }
        })
        .catch((error) => console.error("âŒ Camera/Microphone Error:", error));
}, []); // âœ… Only runs once

useEffect(() => {
  const newPeer = new Peer(); // âœ… Initialize PeerJS
  setPeer(newPeer);

  newPeer.on("open", (id) => {
      console.log(`ðŸŽ¯ Peer ID: ${id}`);
  });

  return () => {
      newPeer.destroy(); // âœ… Cleanup on unmount
  };
}, []); // âœ… Empty dependency array ensures it runs only once
useEffect(() => {
  if (!peer || !localStream) return;

  peer.on("call", (call) => {
      console.log("ðŸ“ž Answering incoming call...");

      call.answer(localStream); // âœ… Ensure we send our stream

      call.on("stream", (remoteStream) => {
          console.log("ðŸ“¹ Received Remote Stream");
          addRemoteVideo(call.peer, remoteStream);
      });

      call.on("error", (err) => {
          console.error("âŒ Call Error:", err);
      });
  });

  return () => {
      peer.off("call"); // âœ… Cleanup event listener
  };
}, [peer, localStream]); 
// âœ… Runs when `peer` or `localStream` changes

const addRemoteVideo = (peerId: string, stream: MediaStream) => {
  console.log(`ðŸ“º Adding video for: ${peerId}`);

  // âœ… Check if video already exists
  if (!remoteVideosRef.current[peerId]) {
      const videoElement = document.createElement("video");
      videoElement.srcObject = stream;
      videoElement.autoplay = true;
      videoElement.playsInline = true;
      videoElement.classList.add("w-48", "h-32", "rounded-lg", "border");

      document.getElementById("remote-videos")?.appendChild(videoElement);
      remoteVideosRef.current[peerId] = videoElement;
  }
};


const callUser = (remotePeerId: string) => {
  if (!peer || !localStream) return;

  console.log(`ðŸ“ž Calling user: ${remotePeerId}`);
  const call = peer.call(remotePeerId, localStream);

  call.on("stream", (remoteStream) => {
      console.log("ðŸ“¹ Received Remote Stream");
      addRemoteVideo(remotePeerId, remoteStream);
  });

  call.on("error", (err) => {
      console.error("âŒ Call Error:", err);
  });
};





  const copyLink = () => {
    if (meetingLink) {
      navigator.clipboard.writeText(meetingLink);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    }
  };

  const toggleMute = () => {
    if (!localStream) return;
    localStream.getAudioTracks().forEach((track) => {
      track.enabled = !track.enabled;
    });
    setIsMuted(!isMuted);
  };

  const toggleVideo = () => {
    if (!localStream) return;
    localStream.getVideoTracks().forEach((track) => {
      track.enabled = !track.enabled;
    });
    setIsVideoOff(!isVideoOff);
  };

  // âœ… Leave Meeting
  const leaveMeeting = () => {
    if (socket) socket.close();
    if (peer) peer.destroy();
    router.push("/");
  };

  return (
    <div className="flex flex-col items-center justify-center h-screen">
      <h2 className="text-xl font-bold">Meeting ID: {meetingId}</h2>

      {meetingLink && (
        <div className="flex flex-col items-center space-y-2 mt-4 bg-white p-4 rounded-lg shadow-lg">
          <p className="text-lg font-semibold">Share this link:</p>
          <div className="flex items-center space-x-2 border p-2 rounded-md w-full">
            <span className="truncate w-64">{meetingLink}</span>
            <button
              className="bg-green-500 text-white px-3 py-1 rounded-md hover:bg-green-600"
              onClick={copyLink}
            >
              {copied ? "Copied!" : "Copy"}
            </button>
          </div>
        </div>
      )}

      <div className="flex space-x-4 mt-4">
        <video ref={myVideoRef} autoPlay muted className="w-48 h-32 border rounded-lg" />
        <div id="remote-videos" className="flex space-x-2"></div>
      </div>

      {/* âœ… Mute, Video & Leave Buttons */}
      <div className="flex space-x-4 mt-6">
        <button
          onClick={toggleMute}
          className="px-4 py-2 bg-gray-500 text-white rounded-md hover:bg-gray-600"
        >
          {isMuted ? "Unmute Mic" : "Mute Mic"}
        </button>

        <button
          onClick={toggleVideo}
          className="px-4 py-2 bg-gray-500 text-white rounded-md hover:bg-gray-600"
        >
          {isVideoOff ? "Turn On Camera" : "Turn Off Camera"}
        </button>

        <button
          onClick={leaveMeeting}
          className="px-4 py-2 bg-red-500 text-white rounded-md hover:bg-red-600"
        >
          Leave Call
        </button>
      </div>
      {isAdmin && pendingUsers.length > 0 && (
        <div className="absolute top-10 right-10 bg-white p-4 rounded-lg shadow-lg">
          <h3 className="text-lg font-bold">Approval Requests</h3>
          {pendingUsers.map((peerId) => (
            <div key={peerId} className="flex justify-between items-center">
              <span>{peerId}</span>
              <button
                onClick={() => approveUser(peerId)}
                className="bg-green-500 text-white px-3 py-1 rounded-md hover:bg-green-600"
              >
                Approve
              </button>
            </div>
          ))}
        </div>
      )}

    </div>
  );
};

export default MeetingPage;
